@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px;
      padding: 0; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.header {
  display: block;
  background-color: #ffffff;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-left: 35px; }
  @media (max-width: 1439px) {
    .header {
      padding-left: 35px; } }

.preloader {
  padding-top: 334px;
  padding-bottom: 100px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #ffffff;
  visibility: visible;
  min-height: calc(100 * var(--vh, 1vh)); }
  @media (max-width: 768px) {
    .preloader {
      padding-top: 477px; } }

.preloader--done {
  visibility: hidden;
  opacity: 0;
  transition: 1s; }

.preloader__logo {
  display: flex;
  justify-content: center; }
  @media (max-width: 768px) {
    .preloader__logo {
      transform: scale(0.7); } }

.preloader__name {
  display: flex;
  width: 347px;
  margin-right: 18px; }

.preloader__name .preloader__letter {
  margin-right: 13px; }

.preloader__name .preloader__letter-g {
  margin-right: 0; }

.preloader__symbol-a {
  display: none;
  margin-right: 35px; }

.preloader__text-top,
.preloader__text-bottom {
  display: flex; }

.preloader__text-top .preloader__text-letter,
.preloader__text-bottom .preloader__text-letter {
  margin-right: 11px; }

.preloader__text-top svg:last-child {
  margin-bottom: -8px; }

.preloader__paragraph {
  font-family: "Neue Machina", "Arial", sans-serif;
  font-size: 24px;
  margin-top: 212px;
  text-align: center; }
  @media (max-width: 320px) {
    .preloader__paragraph {
      margin-top: auto;
      margin-bottom: 0; } }

@media (max-width: 768px) {
  .preloader__paragraph-1 {
    display: none; } }

.preloader__paragraph-2 {
  display: none; }
  @media (max-width: 768px) {
    .preloader__paragraph-2 {
      display: block; } }

.preloader__letter--animate {
  /* visibility: visible; */
  display: block;
  transition: 3.5s;
  animation: preloader-letter 3.5s; }

@keyframes preloader-letter {
  0% {
    opacity: 0.1; }
  30% {
    opacity: 0.5; }
  50% {
    opacity: 0.3; }
  70% {
    opacity: 0.7; }
  100% {
    opacity: 1; } }

.preloader__symbol-a--animate {
  display: block;
  animation: preloader-symbol-a 1s; }

@keyframes preloader-symbol-a {
  0% {
    transform: translate(0, -2000px); }
  70% {
    transform: translate(0, 30px); }
  90% {
    transform: translate(0, -10px); }
  100% {
    transform: translate(0, 0); } }

.preloader__text-letter--animate {
  /* visibility: visible; */
  display: block;
  transition: 3.5s;
  animation: preloader-textletter 3.5s; }

@keyframes preloader-textletter {
  0% {
    opacity: 0.1; }
  20% {
    opacity: 0.5; }
  40% {
    opacity: 0.3; }
  60% {
    opacity: 0.7; }
  80% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

.main-screen {
  display: flex;
  padding-top: 118px;
  justify-content: space-around; }
  @media (max-width: 1024px) {
    .main-screen {
      justify-content: space-between; } }
  @media (max-width: 768px) {
    .main-screen {
      position: relative;
      padding-top: 246px; } }

.main-screen__title {
  margin: 0;
  margin-top: 20px;
  font-size: 96px;
  line-height: 96px;
  font-weight: 400;
  color: #2c39f2;
  width: 562px;
  text-align: left;
  margin-right: 200px;
  margin-top: auto; }
  @media (max-width: 1024px) {
    .main-screen__title {
      margin-right: 0;
      width: 562px; } }
  @media (max-width: 768px) {
    .main-screen__title {
      padding-left: 35px; }
      .main-screen__title--hide {
        display: none; } }

.main-screen__block {
  display: block; }
  @media (max-width: 768px) {
    .main-screen__block {
      display: none;
      display: block;
      width: 100%;
      height: 100%;
      min-height: 950px;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      left: -768px;
      z-index: 100;
      background-color: #2c39f2;
      display: flex;
      flex-direction: column;
      align-items: center; } }

.main-screen__image-coloured {
  margin-bottom: 35px; }
  @media (max-width: 768px) {
    .main-screen__image-coloured {
      display: none; } }

.main-screen__image-white {
  display: none; }
  @media (max-width: 768px) {
    .main-screen__image-white {
      display: block; } }

@media (max-width: 768px) {
  .main-screen__image {
    display: flex;
    justify-content: center;
    margin: 235px auto 0; } }

.main-screen__paragraph {
  margin: 0;
  font-size: 30px;
  line-height: 40px;
  font-weight: 400;
  width: 362px;
  text-align: left;
  margin-top: auto; }
  @media (max-width: 1024px) {
    .main-screen__paragraph {
      width: 352px; } }
  @media (max-width: 768px) {
    .main-screen__paragraph {
      color: #ffffff;
      display: block;
      margin: 35px auto 0; } }

@media (max-width: 768px) {
  .main-screen__block--animate {
    /* visibility: visible; */
    left: 0px;
    animation: main-screen-block 2s; } }

@keyframes main-screen-block {
  0% {
    transform: translate(-576px, 0); }
  30% {
